  /*display: flex;*/
  /*justify-content: flex-start;*/
  height: 90px;
  font-size: 1.0rem;
  position: sticky;
  top: 0;
  z-index: 990;
}

.navbar-container {
  /*display: flex;*/
  /*justify-content: center;*/
  /*align-items: flex-start;*/
  /*height: 85px;*/
  /*max-width: 100%;*/
  display: flex;
  justify-content: flex-start;  /* 保证所有子元素从左侧开始排列 */
  align-items: center; /* 保持垂直居中对齐 */
  height: 85px;
  max-width: 100%;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 80px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  gap: 5px;
}

.navbar-logo > img {
  height: 85px; /*55px*/
}
.navbar img {
  height: 85px; /*55px*/
  background-color: inherit;
}
.imgageCss{

}
.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}
.right-leftNav{
  background: #74A64D;
  width: 33.33vh; /* 如果需要，调整宽度以适应内容 */
  height: 90px; /* 根据需要调整高度 */
  display: flex;
  align-items: center;  /* 垂直居中对齐 */
  /*justify-content: flex-start;  !* 内容靠左对齐 *!*/
  padding-left: 10px; /* 如果需要，可以增加左内边距 */
}

.leftNavbar {
  flex: 1.3;
  justify-content: center;
}
.rightNavbar {
  flex: 0.7;
  color: white;
  justify-content: center;
}
.labelNar{
  color: white;
  width: auto;
}
.triangleNavbar {
  position: absolute;
  top: 50%; /* 将三角形垂直居中 */
  transform: translateY(-33%);
  width: 0;
  height: 0;
  border-style: solid;
  /*border-width: 10px 0 10px 10px; !* 设置三角形的大小和形状 *!
  border-color: transparent transparent transparent white; !* 设置三角形的颜色 *!*/
  border-width: 25px 25px 0 25px; /* 设置三角形的大小和形状 */
  border-color: white transparent white transparent ; /* 设置三角形的颜色 */
}
.nav-menu {
  display: grid;
  margin-left: 20px;
  grid-template-columns: repeat(6, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  margin-top: 50px;
  width: 100vw;
  justify-content: flex-end;
  background-color: black;
  margin-right: 2rem;
  z-index: 999;

}
.nav-menu > li {
  display: inline-block; /* 或使用 flex 布局 */
  color: white; /* 设置文本颜色为白色 */

}
.nav-item {
  height: 80px;
  width: 180px;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover {
  border-bottom: 4px solid #fff;
  transition: all 0.2s ease-out;
}

/*add by dh(leo)*/
.hover-trigger {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  text-align:left;
  padding: 0.5rem 1rem;
  height: 100%;
}
.hover-trigger:hover {
  border-bottom: 4px solid #fff;
  transition: all 0.2s ease-out;
}
.subCatogoryCss{
  margin-top: 28px;
  width: 10%;
}
.hidden-div {
  color: #fff;
  align-items: center;
  text-decoration: none;
  text-align:left;
  padding-left: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  height: 100%;
  width: 160%;
  display: none;
  background-color: #3a5a40;
}
.triangle {
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid white; /* 可以根据需要调整颜色和大小 */
  position: relative;
  top: 6px; /* 调整这里的值以使下标位置合适 */
  display: inline-block;
  vertical-align: middle;
}
.hover-trigger:hover + .hidden-div,
.hidden-div:hover {
  display: block;
}
/*end */

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

