.tShirtDiv {
    display: flex;
    width: 75%;
    margin-left: 10%;
}

.tShirtLeft {
    flex: 0.8;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: left;
    font-size: 30px;
    height: 100%;
}

.placeholder {
    flex-grow: 1;
}


.tShirtRight {
    margin-left: 5%;
    flex: 1.2;
    text-align: center;
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #3a5a40;
    background-size: 100% 100%;
    height: 50vh;
    background-position: center;
    background-repeat: no-repeat;
    /*background-image: url('../../public/images/Dual Side T-shirt Artwork Mockup.png');*/
    background-image: url('../../public/images/Dual Side T-shirt Artwork Mockup - Tiaki Taonga Trust - WHITE.png');
    justify-content: center;
}

.buttonTShirt {
    z-index: 1;
    display: flex;
    margin-top: 1%;
    margin-left: -35%;
    height: 40px;
    width: 160px;
    color: white;
    justify-content: center;
    text-align: center;
    cursor: pointer;
    border: 2px solid white;
    align-items: center;
    background-color: #74a64D;
}

.labelTShirt {
    display: block;
    color: white;
    font-size: 20px;
    text-align: center;
    text-decoration: none;
}

.labelTShirdTitle {
    color: #74a64D;
}
.labelTShirtContent {
    padding-top: 10px;
    color: black;
    font-size: 24px;
}
.labelTShirtFeet {
    padding-top: 10px;
    color: #697477;
    font-size: 18px;
}
