.cards {
  padding: 4rem;
  background: #fff;
}

.cards > h1 {
  text-align: center;
  margin-bottom: 20px;
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}
.cards__wrapper {
  position: relative;
  margin: 50px 0 45px;
}

