.trust-container{

}
.top-div-show-divider {
    flex: 100%;
    padding: 3px;
    box-sizing: border-box;
    margin-right: 50px;
    border-bottom: 1px solid black;
}
.divimg-Contemporary {
    margin-top: -90px;
    position: relative;
    width: 100%;
    height: 40vh;
    overflow: hidden;
    background-size: 100% 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../../public/images/5 Contemporary WAI 262.jpg');
    justify-content: center;
}
.divimg-ContemporaryNew{
    position: relative;
    width: 100%;
    height: 36vh;
    overflow: hidden;
    background-size: 100% 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../../public/images/3b77fcb9-2686-4195-9c45-939a0b630768.jpeg');
    justify-content: center;
}
.text-container{
    margin: 80px 50px 20px 50px;
    font-size: 66px;
    color: white;
}
.labelTile-container{
    font-size: 36px;
    color: black;
}
.labelText-container{
    width: 100%;
    box-sizing: border-box;    padding-top: 20px;
    font-size: 16px;
}
.right-label{
    float: right;
}
.right-Pou-label{
    margin-left: 5px;
    float: right;
}
.fixed-top {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #f0f0f0;
    padding: 20px;
}
.triangleContemporary {
    width: 100%;
    height: 100%;
    border-left: 12px solid transparent;
    border-right: 12px solid transparent;
    border-top: 12px solid black; /* Color and size can be adjusted as needed */
    position: relative;
    top: 0px; /* Adjust the value here so that the subscript is positioned appropriately */
    display: inline-block;
    vertical-align: middle;
}
.content-wrapper {
    margin-top: 20px; /* Make sure the content below is not obscured by the fixed div */
    display: flex;
}

.fixed-leftTrust {
    flex: 22%; /* Fixed width of div on the left  */
    margin-top: 1em;
    margin-left: 50px;
    margin-right: 50px;
    background-color: #d0d0d0;
    box-sizing: border-box;
    padding: 20px;
}
.right-container{
    flex: 78%; /* Fixed width of div on the right */
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}
.scrollable-right {
    margin-top: 1em;
    flex-grow: 1; /* The scrollable div on the right occupies the remaining width */
    /*background-color: #d0d0d0;*/
    margin-right: 50px;
    padding: 20px;
    box-sizing: border-box;
    overflow-y: auto; /* Allow vertical scrolling */
}
.fixed-below-scrollableCon {
    margin-top: 1em;
    margin-right: 50px;
    background-color: #e0e0e0;
    height: 30px;
    width: 100%; /* 设置宽度为100% */
    box-sizing: border-box;
}
.fixed-below-Pou-scrollable-trust {
    position:relative;
    z-index:1;
    margin-right: 50px;
    margin-top: 2em;
    margin-bottom: 1px;
    height: 30px;
    box-sizing: border-box;
}
.contentContemtorary{
    margin-top: 2em;
    font-size: 16px;
    color:gray;
}

