.QAContainer{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 85vh; /* 使容器占满整个视口高度 */
}


.QAContainerDiv {
  position: relative;
  width: 100%;
  height: 85vh;
  background-size: 100% 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../../public/images/3 Learn About Te Pa Whati.jpg');
  justify-content: left;
  overflow: hidden;
}

.maskQA{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* 半透明黑色遮罩 */
  color: white; /* 设置文本颜色为白色 */
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 3;
}
.QAContainerDivTop{
  z-index: 4;
  margin-left: 10%;
}
.fixed-below-QA-scrollable {
  position:relative;
  z-index:5;
  /*margin-right: 50px;*/
  text-align: center;
  margin-top: 2em;
  margin-bottom: 1px;
  height: 30px;
  box-sizing: border-box;
}
.right-QA-label{
  padding-left: 20vh;
  color: white;
  font-size: 25px;
}
.top-div-show-dividerQA {
  flex: 100%;
  padding: 3px;
  box-sizing: border-box;
  margin-right: 50px;
  border-bottom: 2px solid white;
}
